.TodoForm {
  display: -ms-flexbox;
  display: flex;
  background-color: #fafafa;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding: 10px;
}

.TodoForm input {
  -ms-flex: 1 1;
      flex: 1 1;
  outline: none;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.TodoForm input:focus {
  border: 1px solid #206ed3;
}

.TodoForm button {
  margin-left: 10px;
}



